// post请求，返回promise
export function post(url, params) {
    return new Promise((resolve, reject) => {
         axios.post(url, params)
        .then(res => {
            resolve(res.data);
        })
        .catch(err =>{
            reject(err.data)
        })
    });
}

// get 请求， 返回promise
export function get(url){    
    return new Promise((resolve, reject) =>{        
        axios.get(url).then(res => {
            resolve(res.data);
        }).catch(err =>{
            reject(err.data)        
    })    
});}